<template lang="html">
    <div class="fleet">
         <el-table
         	ref="multipleTable"
            :data="tableData3"
            border
            tooltip-effect="dark"
            style="width: 100%"
            @selection-change="handleSelectionChange">
             <el-table-column
            type="selection"
            width="55">
            </el-table-column> 
            <el-table-column
            label="车辆信息"
            width="">
            <template scope="scope">{{ scope.row.msg}}</template>
            </el-table-column>
            <el-table-column
            prop="name"
            label="司机信息"
            width="130">
            <template scope="scope">
              <span>{{scope.row.name}}</span>
              <p>{{scope.row.phone}}</p>
            </template>
            </el-table-column>
            <el-table-column
            prop="address"
            label="定位信息"
            show-overflow-tooltip>
            <template scope="scope">
                <span>{{scope.row.address}}</span> 
               <p>{{scope.row.date}}</p>  
            </template>
            </el-table-column>
            <el-table-column
            prop=""
            label="操作"
            show-overflow-tooltip>
            <template scope="scope">
                <el-button type="primary" style = "" class = "fleet_cx">查询</el-button>
            </template>
            
            </el-table-column>
        </el-table>
    </div>
</template>


<script>
export default {
    data () {
      return {
        radio: '1',
        tableData3: [{
          msg: '沪DG5060 厢式车 9.6米 10吨 30方',
          name: '衣龙魁',
          phone:'15064558893',
          address: '山东省烟台市莱阳市G204(龙门西路)',
          date:'2016-11-17 18:52:10'
        },{
          msg: '沪DG5060 厢式车 9.6米 10吨 30方',
          name: '衣龙魁',
          phone:'15064558893',
          address: '山东省烟台市莱阳市G204(龙门西路)',
          date:'2016-11-17 18:52:10'
        },{
          msg: '沪DG5060 厢式车 9.6米 10吨 30方',
          name: '衣龙魁',
          phone:'15064558893',
          address: '山东省烟台市莱阳市G204(龙门西路)',
          date:'2016-11-17 18:52:10'
        }],
        multipleSelection: []

      };
    },
    methods:{
        handleSelectionChange(val) {
            this.multipleSelection = val;
        },
        vehicle() {
        this.$alert('衣龙魁', '车辆定位',"", {
          confirmButtonText: '确定',
          
        });
      }
    }
}
</script>


<style lang="less" scpode>
    .fleet table{
        font-size: 12px;
    }
    .el-table th>.cell {
      color: #8290B3;
    }
    .el-table__row{
      height: 100px;
        p{
          color:#989BAE;
        }
    }
    .el-message-box{
        width: 600px;
    }
    .el-message-box__btns button:nth-child(2){
        background: green;
        border-color: green;
    }
    .el-button--text{
        color: #4663A9;
        font-size: 12px;
    }
    .color{
        color: #4663A9;
        border-right: 1px solid #D7D7D9;
        padding-right: 4px;
    }
    .color1{
        color: #4663A9;
        border-left: 1px solid #D7D7D9;
        border-right: 1px solid #D7D7D9;
        padding:0 4px;
    }
    .cololr2{
        color:#B4B4B4;
    }
    .fleet_cx{
    	width:80px;
    	height:40px;
    	border:1px solid  green;
    	color: greenyellow;
    	background:#fff
    }
    
</style>


